.App {
  text-align: center;
}

.App-logo {
  height: 50%;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Page {
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 0px;
  margin: 0px;
  font-size: 100%;
}

.NavBar {
  position: fixed;
  width: 100%;
  height: 7%;

  margin-bottom: 1%;
}

.NavBar-Placeholder {
  position: relative;
  width: 100%;
  height: 11%;
}

.Logo {
  position: absolute;
  height: 100%;
  display: inline-block;
  padding: 0%;
  margin: 1%;
}

.Top-Third {
  display: flex;
  flex-direction: row;
  background-color: ;
  padding: 1%;
  height: 31%
}

.Big-Lukas {
  display: inline-block;
  font-size: 15vw;
  height: 30vh;
  width: 100%;
  text-align: left;
  padding: 0px;
  margin: 0px;
}

.Self {
  height: 200%;
}

.Middle-Third {
  display: flex;
  height: 31%;
  padding: 1%;
}

.Bottom-Third {
  display: flex;
  flex-direction: row;
  background-color: ;
  justify-content: center;
  height: 31%;
  padding: 1%;
}

.Big-Viskanta {
  display: inline-block;
  font-size: 15vw;
  height: 30vh;
  width: 100%;
  text-align: right;
  order: 2;
  padding: 0px;
  margin: 0px;
}

.Thanks-For {
  animation: spin 8s infinite linear;
  order: 1;

  max-width: 100%;
  max-height: 100%;
  object-fit:cover;
  padding: 0px;
  margin: 0px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}






